import _ from 'lodash';
import uniqid from 'uniqid'; //uniqid.process().toUpperCase()
import moment from 'moment';

import AgentDeposit from '../../../models/AgentDeposit';
import AgentTransaction from '../../../models/AgentTransaction';
import Agent from '../../../models/Agent';
//import updateAgentStatusAfterOrder from '../../../updateDB/utils/updateAgentStatusAfterOrder';



const purchaseCredit = async (req, res) => {
	try{

		const purchaseCredit = req.body.credit
		const purchasePrice = req.body.price

		let newAgentDeposit = new AgentDeposit({
			owner:{
				superAgent : req.user.superAgent,
				agent : req.user._id
			},
			creditAmount: purchaseCredit,
			priceAmount: purchasePrice,
			orderNumber: uniqid.process().toUpperCase()
		})
		await newAgentDeposit.save()

		const agent = await Agent.findOneAndUpdate({ _id: req.user._id }, { '$inc': { 'currentStatus.credit': purchaseCredit, 'currentStatus.availableCredit': purchaseCredit }}, { new: true })

		let newAgentTransaction = new AgentTransaction({
			owner:{
				superAgent : req.user.superAgent,
				agent : req.user._id,
			},
			orderType: 'Deposit',
			transactionType: 'in',
			creditAmount: purchaseCredit,
			resultAmount: agent.currentStatus.credit,
			orderNumber: newAgentDeposit.orderNumber
		})
		await newAgentTransaction.save()


		res.json(req.user)
	}catch(err){
		throw err
	}
}

export default purchaseCredit;




